@import url("https://fonts.googleapis.com/css?family=Exo:800&display=swap");
@import url("https://fonts.googleapis.com/css?family=Lato:900|Noto+Sans+KR&display=swap");

* {
	box-sizing: border-box;
}

#root,
body,
html {
	height: 100%;
}

body {
	margin: 0;
	font-family: "Apple SD Gothic Neo", "Malgun Gothic", "Nanum Gothic",
		sans-serif;
}

/** APP COMPONENT **/

a:link,
:visited {
	text-decoration: none;
}

#fixed-bar {
	background-color: #fff;
	position: fixed;
	width: 100%;
	margin: 0 auto;
	top: 0;
	left: 0;
	right: 0;
	z-index: 100;
	box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.1);
	-webkit-box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.1);
	-moz-box-shadow: 0 3px 5px 0 rgba(0, 0, 0, 0.1);
}

#fixed-bar-wrap {
	max-width: 1024px;
	margin: 0 auto;
	justify-content: space-between;
	position: relative;
	display: flex;
	align-items: center;
}

#logo-wrap {
	margin: 10px;
	display: inline-block;
}

#home-link {
	display: flex;
	align-items: center;
}

.logo-img {
	display: inline-block;
	width: 40px;
	height: 40px;
	margin-right: 10px;
}

.logo-text {
	display: inline-block;
	color: #0053bf;
	font-family: "Exo";
	font-size: 20px;
}

#category-wrap {
	display: inline-block;
}

.category {
	font-family: "Exo";
	font-size: 20px;
	display: inline-block;
	margin-left: 20px;
}

.category a:visited {
	color: #0053bf;
	opacity: 0.5;
}

.glow {
	color: #0053bf;
	opacity: 0.5;
	transition: opacity 0.15s ease-in;
}

.glow:hover,
.glow:focus {
	opacity: 1;
}

footer {
	background-color: #f8f8f8;
	margin-top: auto !important;
	text-align: center;
	position: relative;
	border-top: 1px solid #dbdbdb;
}

.footer-container {
	padding: 20px 0;
	text-align: center;
}

.footer-container a {
	color: #fff;
	margin: 0 10px;
}

.footer-icons {
	text-align: center;
}

.footer-copyright {
	color: #939393;
	font-size: 15px;
}

/** HOME COMPONENT **/

.home-img-section {
	position: relative;
	padding-top: 100px;
	background-color: #193b84;
	color: #fff;
	text-align: center;
	height: 200px;
	background-image: url(./home-img.png);
	background-repeat: no-repeat;
	background-position: right;
	background-size: 200px 200px;
}

h1 {
	font-size: 40px;
	display: inline-block;
	margin: 0 auto;
}

.infection-wrap {
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
	font-family: "Noto Sans KR", sans-serif;
}

.infection-data {
	margin-bottom: 160%;
}

.createTime {
	color: gray;
	font-size: 15px;
}

.infection-true {
	margin-top: 15%;
	color: red;
	font-size: 20px;
}

.infection-true:after {
	content: "";
	display: block;
	width: 60px;
	border-bottom: 2px solid #bcbcbc;
	margin: 15px auto;
}

.infection-dead {
	color: black;
	margin-top: 10%;
	font-size: 20px;
}

.infection-dead:after {
	content: "";
	display: block;
	width: 60px;
	border-bottom: 2px solid #bcbcbc;
	margin: 15px auto;
}

.infection-false {
	color: green;
	margin-top: 10%;
	font-size: 20px;
}

/** City Location **/

.map {
	display: inline-block;
	position: relative;
	width: 600px;
	height: 750px;
	background-image: url(./korea-map.png);
	background-repeat: no-repeat;
	background-position: center;
	background-size: 500px 700px;
	margin: 50px 0 0 30px;
	font-family: "Noto Sans KR", sans-serif;
}

.city-common {
	position: absolute;
	height: 50px;
	width: 45px;
	font-size: 14px;
	font-weight: bold;
	background-color: white;
	color: #494949;
	box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.15);
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.city-common:hover {
	cursor: pointer;
	box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.3);
	-webkit-box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.2);
	-moz-box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.2);
	transform: translateY(-1.5px);
	background-color: #c4ebff;
}

