/** TIMESALE COMPONENT **/

.head-section {
	position: relative;
	padding-top: 100px;
	background-color: #193b84;
	color: #fff;
	text-align: center;
	height: 200px;
	margin-bottom: 20px;
}

.head-section-text {
	display: inline-block;
	margin-left: 5%;
	transform: translateY(-7px);
}

.mask-section {
	align-items: center;
	display: flex;
}

.mask-wrap {
	align-items: center;
	border-radius: 8px;
	border: 1px solid #e9ecef;
	width: 60%;
	margin: 20px auto;
	display: inline-flex;
}

.mask-wrap:hover {
	cursor: pointer;
	box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.2);
	-webkit-box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.2);
	-moz-box-shadow: 0 6px 10px 0 rgba(0, 0, 0, 0.2);
	transform: translateY(-1.5px);
}

.mask-img {
	display: inline-block;
	width: 200px;
	height: 200px;
	margin-right: 30px;
	border-top-left-radius: 8px;
	border-bottom-left-radius: 8px;
	background-repeat: no-repeat;
	background-size: cover;
	flex: none;
}

.item-details {
	display: inline-block;
}

.item-price {
	color: #0053bf;
}

.item-time {
	color: #d30000;
	align-items: center;
	display: flex;
}

.clock {
	margin-top: 3px;
	margin-right: 5px;
}

.item-sub {
	color: #7a7a7a;
}

.item-timer {
	align-items: center;
	border-radius: 8px;
	width: 30%;
	margin: 30px 10% 30px 0;
	display: inline-flex;
	color: white;
	background-color: red;
}

.item-selling {
	align-items: center;
	border-radius: 8px;
	width: 30%;
	margin: 30px 10% 30px 0;
	display: inline-flex;
	color: white;
	background-color: #19c100;
}

.item-soldout {
	align-items: center;
	border-radius: 8px;
	width: 30%;
	margin: 30px 10% 30px 0;
	display: inline-flex;
	color: white;
	background-color: #3d3d3d;
}

/* OFFICIALSALE COMPONENT*/

.officialsale-body {
	height: 100%;
}

.officialsale-section {
	position: relative;
	padding-top: 100px;
	background-color: #193b84;
	color: #fff;
	text-align: center;
	height: 200px;
}

.officialsale-section-text {
	display: inline-block;
	margin-left: 5%;
	transform: translateY(-7px);
}

.kakao-map-wrap {
	height: 100%;
}

#kakao-map {
	width: 100%;
	height: 100%;
}

.store-label {
	display: inline-block;
	min-width: 40px;
	padding: 3px;
	font-size: 13px;
	font-weight: 500;
	color: #fff;
	text-align: center;
	position: relative;
	font-family: "Noto Sans KR", sans-serif;
}

.store-label:hover + .store-details {
	opacity: 1 !important;
}

.store-details {
	display: inline-block;
	text-align: left;
	padding: 3px;
	min-width: 200px;
	background-color: #262626;
	color: #fff;
	border-radius: 3px;
	position: absolute;
	transform: translate(-120%, -110%);
	transition: opacity 0.2s;
	opacity: 0;
	font-family: "Noto Sans KR", sans-serif;
}

.text-long {
	font-size: 10px !important;
}

.backbox {
	position: relative;
	background-color: rgba(0, 0, 0, 0.5);
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: 10;
}

.map-modal {
	position: fixed;
	top: 60%;
	left: 50%;
	transform: translate(-50%, -50%);
	color: #000;
	width: 510px;
	height: 320px;
	/*translate 효과 줬을때 max,min으로 크기 설정하면 
    텍스트가 blur처리됨. 아직 해결법 못찾음*/
	z-index: 9999;
	font-size: 15px;
	align-items: center;
	justify-content: center;
	background-color: #fff;
	padding: 15px;
	font-family: "Noto Sans KR", sans-serif;
	border-radius: 10px;
}

.modal-head {
	font-size: 20px;
}

.modal-contents {
	font-size: 13px;
}

.modal-footer {
	font-size: 11px;
}

.btn-agree {
	color: #fff;
	background-color: #0053bf;
	box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.2);
	margin-top: 15px;
	font-size: 15px;
	font-family: "Noto Sans KR", sans-serif;
	border: none;
	border-radius: 4px;
	float: right;
	cursor: pointer;
}

.date-modal {
	position: fixed;
	top: 220px;
	left: 15px;
	z-index: 9999;
	background-color: #fff;
	padding: 10px;
	border-radius: 10px;
	box-shadow: 0 3px 7px 0 rgba(0, 0, 0, 0.3);
	font-family: "Noto Sans KR", sans-serif;
}

.modal-show {
	display: flex;
	align-items: center;
	justify-content: center;
}

.modal-show h4 {
	margin: 5px;
	color: #4f4f4f;
}

.modal-show button {
	display: inline-block;
	height: 25px;
	margin-left: 5px;
	border: none;
	border-radius: 3px;
	color: #fff;
	background-color: #193b84;
	font-family: "Noto Sans KR", sans-serif;
	cursor: pointer;
}

.modal-hidden {
	display: none;
	border-top: dashed 1px #b7b7b7;
	margin-top: 10px;
}

.birth {
	color: #193b84;
	font-weight: bold;
	text-align: center;
	margin-top: 5px;
}

.day-ref {
	color: #a0a0a0;
	font-size: 10px;
	margin: 5px auto 0;
	text-align: center;
}

.day {
	text-align: center;
	font-size: 20px;
	color: #7171ff;
	margin: 0 auto;
}

.birth-input {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	text-align: center;
	border: solid 2px;
	border-color: #b7b7b7;
	border-radius: 5px;
	height: 30px;
	margin-top: 5px;
}

.birth-submit {
	display: block;
	width: 100%;
	height: 30px;
	margin-top: 5px;
	border: none;
	border-radius: 5px;
	color: #fff;
	background-color: #7171ff;
	font-family: "Noto Sans KR", sans-serif;
}

.modal-hidden p {
	color: #a0a0a0;
	font-size: 10px;
	margin: 5px auto 0;
	text-align: center;
}

/* SUPPORT COMPONENT*/

.waiting {
	width: 100%;
	height: 600px;
	font-size: 25px;
	font-weight: bold;
	text-align: center;
	line-height: 600px;
}
